}
.navbar-container-transparent {
  height: 90px;
  width: 100vw;
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 99;
  background: var(--blue-marine);
 
}

.main-container {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  width: 50vw;
}

.nav-logo {
  align-items: center;
  cursor: pointer;
  text-decoration: none;
  flex-grow: 1;
  display: flex;
}

.nav-menu {
  display: flex;
  list-style: none;
  text-align: center;
  margin-right: 2rem;
  margin-top: auto !important;
  margin-bottom: auto !important;
}

.link {
  color: white;
  text-decoration: none;
  position: relative;
  transition: all ease-in-out 250ms;
  text-shadow: -1px 1px 0px rgb(0 0 0 / 30%), -2px 4px 2px rgb(0 0 0 / 10%);
  font-family: proxima-nova;
}
.link:hover{
  color: var(--blue-ciel);
  text-decoration: none;
}

.link-moved {

  text-decoration: none;
  position: relative;
  transition: all ease-in-out 250ms;
  font-weight: 400;
  font-family: proxima-nova;
  text-shadow: -1.2px 1.2px 0px rgb(0 0 0 / 30%), -2px 4px 2px rgb(0 0 0 / 10%);
}

.link::after {
  content: "";
  position: absolute;
  display: block;
  height: 0.09rem;
  width: 0%;
  background:   linear-gradient(
    18deg,
    rgba(31, 33, 45, 1) 0%,
    rgba(46, 163, 221, 1) 61%
  );;
  bottom: -0.75rem;
  transition: all ease-in-out 250ms;
}

.link:hover::after {
  width: 100%;
  background: linear-gradient(
    18deg,
    rgba(31, 33, 45, 1) 0%,
    rgba(46, 163, 221, 1) 61%
  );
}



.link-moved::after {
  content: "";
  position: absolute;
  display: block;
  height: 0.09rem;
  width: 0%;

  bottom: -0.75rem;
  transition: all ease-in-out 250ms;
}

.link-moved:hover::after {
  width: 100%;
}


.fa-code {
  margin-left: 1rem;
}

.navigation-item {
  padding: 5px 20px;
  color: var(--blue-ciel);
  font-size: 1rem;
  font-weight: lighter;
}

.navigation-item-moved {
  padding: 5px 20px;
  font-size: 1rem;
  font-weight: lighter;
}

.nav-icon {
  display: none;
}
.nav-icon-moved {
  display: none;
}

.logo {
  display: flex;
width: 15%;
  margin-left: 10%;
 
}



.profileIcon{
  width: 40px;
}
