.speakers_body{
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;

    width: 20vw;
    height: 20vh;
}
.slider_speakers{
    position: relative;
    width: 200px;
    height: 200px;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-animation: rotate 30s linear infinite;
            animation: rotate 30s linear infinite;
}
@-webkit-keyframes rotate{
    0%{
        -webkit-transform: perspective(1000px) rotateY(0deg);
                transform: perspective(1000px) rotateY(0deg);
    }
    100%{
        -webkit-transform: perspective(1000px) rotateY(360deg);
                transform: perspective(1000px) rotateY(360deg);
    }
}
@keyframes rotate{
    0%{
        -webkit-transform: perspective(1000px) rotateY(0deg);
                transform: perspective(1000px) rotateY(0deg);
    }
    100%{
        -webkit-transform: perspective(1000px) rotateY(360deg);
                transform: perspective(1000px) rotateY(360deg);
    }
}
.slider_speakers span{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    -webkit-transform-origin: center;
            transform-origin: center;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transform: rotateY(calc(var(--i)*90deg)) translateZ(350px);
            transform: rotateY(calc(var(--i)*90deg)) translateZ(350px);
}
.slider_speakers span div{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;

    width: 100%;
    height: 100%;
    border-radius: 0%;
    object-fit: cover;
    transition: 2s;
}

.slider_speakers span:hover div{
    -webkit-transform: translate(-50px) scale(1.2) translateY(-25px);
            transform: translate(-50px) scale(1.2) translateY(-25px);
}
@media screen and (max-width: 700px) {
    .slider_speakers{
        width: 100px;
        height: 100px;
    }
    .slider_speakers span div img{
        width: 100px;
        height: 100px;
    }
    .slider_speakers span{
        -webkit-transform: rotateY(calc(var(--i)*90deg)) translateZ(180px);
                transform: rotateY(calc(var(--i)*90deg)) translateZ(180px);
    }
    
}

