.containerPartenaire {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.boxPartenaire {
  border-radius: 12px;
  position: relative;
  width: 300px;
  height: 380px;
  background: #f8f9fc;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

/* .contentText:hover {
 padding-right: 5px;
  overflow-y: auto;
  max-height: 300px;

} */

.boxPartenaire:hover .contentText {
  padding-right: 5px;
   overflow-y: auto;
   max-height: 300px;
 
 }



 ::-webkit-scrollbar{
  width: 3px;
  height: 3px;
  
  }
  ::-webkit-scrollbar-thumb{
  background: #B3AFB3;
  border-radius: 0px;
  }
  ::-webkit-scrollbar-thumb:hover{
  background: #B3AFB3;
  }
  ::-webkit-scrollbar-track{
  background: #F0F0F0;
  border-radius: 0px;
  box-shadow: inset 0px 0px 0px 0px #F0F0F0;
  }

.boxPartenaire .imgBx {
  width: 100%;
  height: 100%;
}

.boxPartenaire .imgBx img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  object-fit: cover;
  transition: 0.5s;
}

.boxPartenaire:hover .imgBx img {
  opacity: 0;
  border-radius: 12px;
}

.boxPartenaire .content {
  position: absolute;
  bottom: 20px;
  left: 10%;
  width: 80%;
  height: 60px;
  background: #fff;
  transition: 0.5s;
  overflow: hidden;
  border-radius: 12px;
  padding: 15px;
  box-sizing: border-box;
}

.boxPartenaire:hover .content {
  width: 100%;
  height: 100%;
  bottom: 0;
  left: 0;
}

.boxPartenaire .content h3 {
  margin: 0;
  padding: 0;
  font-size: 20px;
}

.boxPartenaire .content p {
  margin: 10px 0 0;
  padding: 0;
  opacity: 0;
  line-height: 1.2em;
  transition: 0.5s;
  text-align: justify;
}

.boxPartenaire:hover .content p {
  opacity: 1;
  transition-delay: 0.5s;
}
