.edition {
  box-sizing: border-box;
  padding: 0 1rem;
  height: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-top: 0;
  background-color: var(--beige);
  mix-blend-mode: screen;

  font-size: calc(2rem + 6vw);
  font-weight: 900;
  letter-spacing: -0.15rem;
  line-height: 0.9;
  text-align: right;
  text-transform: uppercase;
  font-family: "Gilroy-Bold";
}

.edition h1 {
  font-size: 70px;
}

.home-header-content {
  height: 100vh;
  width: auto;
  display: flex;
}

.button {
  color: #fff;
  transition: all 0.5s;
  position: relative;
  background-color: rgb(31, 33, 45, 0.3);
  padding: 1rem 2rem;
  border-radius: 15px;
}

.button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  background: color #2ea3dd;
  transition: all 0.3s;
}

.button:hover::before {
  opacity: 0;
  -webkit-transform: scale(0.5, 0.5);
          transform: scale(0.5, 0.5);
}

.button::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0;
  transition: all 0.3s;
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 15px;
  -webkit-transform: scale(1.2, 1.2);
          transform: scale(1.2, 1.2);
}

.button:hover::after {
  opacity: 1;
  -webkit-transform: scale(1, 1);
          transform: scale(1, 1);
}

a {
  text-decoration: none;
}



.social-links {
  display: flex;
  position: absolute;
  left: 1%;
  bottom: 1%;
}

.social-links a {
  width: 50px;
  height: 50px;
  text-align: center;
  text-decoration: none;
  color: #000;
  box-shadow: 0 0 20px 10px rgba(0, 0, 0, 0.05);
  margin: 0 5px;
  border-radius: 50%;
  position: relative;
  overflow: hidden;
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
  display: flex;
  justify-content: center;
  align-items: center;
}

.social-links a .fab {
  font-size: 30px;
  line-height: 80px;
  position: relative;
  z-index: 10;
  transition: color 0.5s;
}

.social-links a::after {
  content: "";
  width: 100%;
  height: 100%;
  top: -90px;
  left: 0;
  background: #000;
  background: linear-gradient(
    18deg,
    rgba(31, 33, 45, 1) 0%,
    rgba(46, 163, 221, 1) 61%
  );
  position: absolute;
  transition: 0.5s;
}

.social-links a:hover::after {
  top: 0;
}

.social-links a:hover .fab {
  color: #fff;
}

.social-links a:hover {
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
}


